@import url(https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,400;0,700;1,700&display=swap);
header {
    width: 100%;
    height: 100vh;
    background-color: #8bc6ec;
    background-image: linear-gradient(135deg, #8bc6ec 0%, #9599e2 100%);
}

.bars {
    width: 80%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    height: 8vh;
}

.bars a {
    text-decoration: none;
}

.bars a p {
    font-size: 2rem;
    color: black;
}

.navigation ul {
    display: flex;
}

.navigation ul li {
    list-style: none;
}

.navigation ul li a {
    text-decoration: none;
    font-size: 1.4rem;
    color: black;
    margin: 0 1rem;
}

.navigation ul li a:hover {
    border-bottom: 2px solid black;
}

.line {
    width: 28px;
    height: 4px;
    margin: 4px;
    background-color: black;
    border-radius: 8px;
}

.burger {
    display: none;
}

@media (max-width: 768px) {
    .bars {
        width: 98%;
    }

    body {
        overflow-x: hidden;
    }

    .burger {
        display: block;
        cursor: pointer;
    }

    .navigation {
        position: absolute;
        top: 8vh;
        right: 0;
        height: calc(100% - 8vh);
        width: 70%;
        background-color: rgba(0, 0, 0, 0.8);
        transform: translateX(100%);
    }

    .navigation ul {
        height: 100%;
        flex-direction: column;
        justify-content: space-around;
        text-align: center;
    }

    .navigation ul li a {
        color: white;
    }

    .menu__active {
        transform: translateX(0);
    }
}

.info {
    height: 92vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.info img {
    width: 200px;
    object-fit: cover;
    border-radius: 50%;
    margin-bottom: 1rem;
}

* {
    margin: 0;
    padding: 0;
}

*,
*:before,
*:after {
    box-sizing: border-box;
}

body,
html {
    font-family: "Ubuntu", sans-serif;
    overflow: hidden;
}

